.header {
  height: 100px;
}

.header .logo {
  width: 125px;
}

.header__info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-left: 50px;
  margin-bottom: 20px;
}

.header__info div {
  display: flex;
  align-items: center;
}

.header__info div i {
  color: #c59850;
  margin-right: 12px;
}

.header__info div p {
  color: rgba(40, 40, 40, 0.6);
  text-transform: uppercase;
  font-size: 11px;
  letter-spacing: 1px;
}

.header nav {
  margin-left: 50px;
}

.header nav ul {
  display: flex;
  justify-content: space-between;
}

.header nav ul a {
  text-decoration: none;
  color: rgba(14, 14, 14, 0.7);
  text-transform: uppercase;
  font-size: 12px;
  font-weight: 500;
  display: flex;
  align-items: center;
  letter-spacing: 1px;
}

.header nav ul a div {
  position: relative;
  font-size: 11px;
  letter-spacing: 0.6px;
  margin-left: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #ffffff;
}

.header nav ul a div i {
  font-size: 19px;
  color: #af0808;
  position: absolute;
  z-index: -1;
}

.header nav ul a div span {
  padding-left: 10px;
  padding-bottom: 1px;
}

.header__phone {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__phone a {
  font-size: 25px;
  font-weight: 300;
  letter-spacing: 0.6px;
  text-decoration: none;
  color: #282828;
  border-bottom: 1px dashed #282828;
  position: relative;
  top: -3.5px;
}

.header__phone p {
  text-transform: uppercase;
  font-size: 10px;
  letter-spacing: 1px;
  color: rgba(14, 14, 14, 0.5);
  width: 32%;
  line-height: 1.5;
}

.header__toolbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__toolbar a i {
  font-size: 20px;
  color: #646464;
}

.header__toolbar .cart {
  position: relative;
}

.header__toolbar .cart i {
  font-size: 25px;
}

.header__toolbar .cart__count {
  width: 18px;
  height: 18px;
  border-radius: 50%;
  background: #c59850;
  color: #ffffff;
  font-size: 10px;
  font-weight: 500;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: -5px;
  right: -5px;
}

.top__banner {
  height: 35px;
  background-image: url("../img/top-banner.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
  color: #ffffff;
}

.top__banner .container {
  position: relative;
}

.top__banner p {
  font-weight: 900;
  font-size: 11px;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.top__banner a {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  text-decoration: none;
  color: #ffffff;
  text-transform: uppercase;
  font-size: 10px;
  letter-spacing: 1px;
}

.top__banner a i {
  font-size: 21px;
  color: #c59850;
  position: absolute;
  z-index: 1;
}

.top__banner a span {
  position: relative;
  z-index: 2;
}

.top__banner .close {
  width: 35px;
  height: 35px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  right: 0;
  top: 0;
  background: #c59850;
}

.top__banner .close i {
  font-size: 14px;
}

body {
  font-family: 'Lato';
}

.container {
  width: 100%;
  max-width: 1200px;
  height: 100%;
}

.row {
  height: 100%;
}
